*{
  margin: 0;
  padding: 0;
}
input{
  border: none;
  height: 24px;
  margin: 5px;
  box-shadow: 0 3px 5px grey;
}
input[type=checkbox]{
  border: none;
  height: 15px;
  margin: 5px;
  box-shadow: 0 3px 5px grey;
}
input[type=radio]{
  height: auto;
  width: auto;
  box-shadow: none;
}
select{
  border: none;
  height: 24px;
  margin: 5px;
  box-shadow: 0 3px 5px grey;
}
.App {
  display: flex;
  position: relative;
  text-align: center;
  background-image: url('./images/FundoLogin.jpeg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  width: 100%;
}

.op-minimizadas{
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap-reverse;
  align-items: end;
  align-content: end;
  right: 0;
  position: fixed;
}

.minimizado{
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  color: white;   
  background-color: #064A8B;
  border: 1px solid white;
  border-radius: 5px;
  height: 50px;
  width: 100px;
}
.minimizado:hover{
  cursor: pointer;
}

@media screen and (max-width: 425px){
  .App{
    height: 95vh;   
    width: auto;
    align-items: start;
  }
  .op-minimizadas{
    top: 5%;
    right: 5%;
    height: 90%;
  }
  
}